@import "../../scss/fonts.scss";
@import "../../scss/mixins.scss";

.root{
  padding: 14px 45px;
  background: $yellow;
  border-radius: 25px;
  border: 2px solid $yellow;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  span{
    @include fontChar($wh, 25px, 700);
    position: relative;
    z-index: 3;
  }
  &:hover {
    &::before{
      width: 100%;
      left: 0;
      right: auto;
    }
  }
  &::before{
    content: '';
    width: 0;
    transition: .5s ease;
    height: 100%;
    background: $blue;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
  }
}