@import '../../scss/fonts.scss';
@import '../../scss/mixins.scss';

.header{
  width: 100%;
  padding: 23px 0;
  z-index: 500;
  background: $wh;
  &.menu__fixed{
    position: fixed;
    top: 0;
    left: 0;
  }
  .container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .left{
      display: flex;
      align-self: center;
      justify-content: center;
      .logo__wrapper{
        margin-right: 130px;
        display: flex;
        align-items: center;
        justify-content: center;
        a{
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
      @media (max-width: 993px) {
        display: none;
      }
    }
    .nav__menu{
      display: flex;
      align-items: center;
      justify-content: center;
      background: white;

      @media (max-width: 992px) {
        position: absolute;
        top: 100%;
        left: -100%;
        min-height: 100vh;
        flex-direction: column;
        align-items: center;
        width: 100%;
        transition: all .7s ease;
        z-index: 100;
        background: $wh;
        padding: 1rem 0;
        justify-content: flex-start;
      }
      &.nav__menu__active{
        left: 0;
      }
      .nav__item{
        position: relative;
        cursor: pointer;
        &:not(:last-child) {
          margin-right: 56px;
          @media (max-width: 992px) {
            margin-right: 0;
          }
        }
        @media (max-width: 992px) {
          margin-right: 0;
          padding: 20px 0;
          &:not(:nth-child(4)) {
            border-bottom: 1px solid #D9D9D9;
          }
          width: 90%;
          display: flex;
          justify-content: center;
        }
        span{
          @include fontChar($black, 18px, 600);
          border-bottom: 2px solid transparent;
          transition: all .3s ease;
          @media (max-width: 992px) {
            border-bottom: none;
          }
        }
        &:hover span {
          color: $yellow;
          padding-bottom: 5px;
          border-bottom-color: $yellow;
          @media (max-width: 992px) {
           padding: 0;
          }
        }
        &:before{
          content: '';
          position: absolute;
          top: -5px;
          bottom: -5px;
          left: -5px;
          right: -5px;
          @media (max-width: 992px) {
            display: none;
          }
        }
      }
    }
    .contact_us_btn{
      position: relative;
      padding: 10px 20px;
      background: $blue;
      border-radius: 100px;
      overflow: hidden;
      display: flex;
      @media (max-width: 992px) {
        margin-top: 50px;
      }
      span{
        @include fontChar($wh, 16px, 600);
        position: relative;
        z-index: 2;
      }
      &:hover {
        &::before{
          width: 100%;
          left: 0;
          right: auto;
        }
      }
      &::before{
        content: '';
        width: 0;
        transition: .5s ease;
        height: 100%;
        background: $yellow;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;
        border-radius: 100px;
      }
    }
    .menu__mobile{
      width: 28px;
      height: 26px;
      position: relative;
      overflow: hidden;
      cursor: pointer;
      display: none;
      @media (max-width: 992px) {
        display: block;
      }
      &.menu__active{
        span{
          &:nth-child(1) {
            transform: rotate(135deg);
            top: 50%;
          }
          &:nth-child(3) {
            transform: rotate(-135deg);
            top: 50%;
          }
          &:nth-child(2) {
            transform: translateX(100%);
          }
        }
      }
      &::before{
        content: '';
        top: -10px;
        bottom: -10px;
        left: -10px;
        right: -10px;
        position: absolute;
      }
      span{
        position: absolute;
        right: 0;
        height: 4px;
        width: 28px;
        border-radius: 5px;
        transition: all .3s ease;
        background: $blue;
        &:nth-child(1) {
          top: 0;
        }
        &:nth-child(2) {
          top: 50%;
          transform: translateY(-50%);
          background: $yellow;
        }
        &:nth-child(3) {
          bottom: 0;
        }
      }
    }
  }
}