@import '../scss/mixins.scss';
@import '../scss/fonts.scss';

.intro{
  width: 100%;
  margin-top: 1rem;
  position: relative;
  padding-bottom: 6rem;
  .background__figure{
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
    top: 10rem;
    left: 50%;
    transform: translateX(-50%);
    &::before{
      content: '';
      background: url("../assets/img/intro__background.svg") no-repeat center / cover;
      width: 64rem;
      height: 44rem;
      position: absolute;
      right: -4rem;
      @media (min-width: 992px) and (max-width: 1300px) {
        @include adaptive-value("width", 63, 35, 1);
        @include adaptive-value("height", 44, 24, 1);
      }
      @media (max-width: 992px) {
        width: 36rem;
        height: 25rem;
        right: -7rem;
      }
      @media (max-width: 600px) {
        width: 21.5rem;
        height: 15rem;
        right: -6rem;
      }
    }
  }
  @media (max-width: 786px) {
    margin-top: 0;
  }
  .container{
    position: relative;
    padding-top: 10rem;
    @media (max-width: 992px) {
      padding-top: 4rem;
    }
    @media (max-width: 786px) {
      padding-top: 1rem;
    }
    .text__wrapper{
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media (max-width: 992px) {
        justify-content: center;
        flex-direction: column-reverse;
      }
      span{
        width: 30%;
        @media (max-width: 992px) {
          display: none;
        }
      }
      .intro_desc {
        width: 100%;
        max-width: 11rem;
        margin-right: 3rem;
        @include fontChar($black, 32px, 500);
        @media (max-width: 992px) {
          @include fontChar($black, 20px, 500);
          max-width: 100%;
          text-align: center;
          margin-right: 0;
          margin-top: 20px;
        }
        @media (max-width: 530px) {
          margin-top: 0;
        }
      }
      svg{
        width: 440px;
        height: 126px;
        @media (min-width: 992px) and (max-width: 1090px ){
          width: 340px;
        }
        @media(max-width: 786px) {
          @include adaptive-value("width", 50, 16, 1);
        }
      }
    }
    .image__figure{
      position: absolute;
      top: 2rem;
      right: 0;
      display: flex;
      @media (max-width: 992px) {
        top: 14rem;
        right: 6rem;
      }
      img{
        @media (max-width: 992px) {
          @include adaptive-value("width", 17, 10, 1);
        }
        @media (max-width: 786px) {
          display: none;
        }
      }
    }
    .central__btns{
      width: 100%;
      display: flex;
      justify-content: center;
      margin-top: 3.5rem;
      @media (max-width: 786px) {
        margin-top: 2rem;
      }
      a{
        color: transparent;
        padding: 38px;
        border-radius: 30px;
        display: flex;align-items: center;
        justify-content: center;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
        backdrop-filter: blur(6.5px);
        @media (max-width: 786px) {
          padding: 27px;
          border-radius: 20px;
        }
        transition: background .3s ease;
        &:nth-child(1) {
          margin-right: 13px;
          transition: background .3s ease;
          transition: all .3s ease;
          background: linear-gradient(180deg, #08BDFF 0%, rgba(8, 189, 255, 0) 135%);
          &:hover {
            background: linear-gradient(180deg, #08BDFF 0%, $blue 135%);
          }
        }
        &:nth-child(2) {
          background: linear-gradient(180deg, #EAAA31 0%, rgba(234, 170, 49, 0) 100%);
          &:hover {
            background: linear-gradient(0, #EAAA31 0%, $yellow 100%);
          }
        }
      }
    }
  }
  .about__us__wrapper{
    padding: 0 23px;
  }
  .about__us{
    display: flex;
    position: relative;
    left: 50%;
    transform: translate(-50%, 10rem);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 2px solid #BFC9C9;
    border-radius: 39px;
    padding: 2rem 5rem;
    background: linear-gradient(9.2deg, #FFFFFF -12.57%, rgba(217, 217, 217, 0) 105.63%);
    box-shadow: 0px 47px 42px -20px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(15px);
    width: 100%;
    max-width: 860px;
    @media (max-width: 992px) {
      padding: 2rem;
    }
    @media (max-width: 786px) {
      transform: translate(-50%, 3rem);
    }
    *{
      text-align: center;
    }
    h1{
      @include fontChar($black, 50px, 700);
      margin-bottom: 1rem;
      @media (max-width: 400px) {
        font-size: 40px;
      }
    }
    p{
      @include fontChar(#4E4E4E, 18px, 400);
      margin-bottom: 2rem;
    }
    .about_btn{
      @media (max-width: 400px) {
        font-size: 21px;
      }
    }
  }
}

.projects {
  margin-top: 150px;
  @media(max-width: 787px) {
    margin-top: 20px;
  }
  .container{
    display: block;
    position: relative;
    .tabs{
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      .switch__block{
        align-self: flex-start;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 20px;
        button{
          border: none;
          @include fontChar(#787878, 18px, 700);
          position: relative;
          cursor: pointer;
          transition: color .3s ease;
          &:hover {
            color: $blue !important;
          }
          @media (max-width: 400px) {
            padding-right: 12px;
            margin-right: 7px;
            font-size: 14px;
          }
          &:hover {
            color: $blue;
          }
          &.active_btn{
            color: $blue;
          }
          &:not(:last-child) {
            padding-right: 17px;
            margin-right: 10px;
            &::before {
              content: '';
              width: 7px;
              position: absolute;
              height: 7px;
              flex: 0 0 7px;
              border-radius: 50%;
              background: $blue;
              top: 50%;
              transform: translateY(-50%);
              right: 0;
            }
          }
        }
      }
      .content__tabs{
        width: 100%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 130px;
        margin-top: 3rem;
        transition: all .4s ease;
        @media(max-width: 992px) {
          display: flex;
          gap: 0;
          flex-wrap: wrap;
          justify-content: space-between;
        }
        @media(max-width: 787px) {
          justify-content: center;
          align-items: center;
        }
        .col{
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-between;
          @media(max-width: 992px) {
            width: 48%;
          }
          @media(max-width: 787px) {
            width: 100%;
          }

          img{
            margin-bottom: 1rem;
          }
          .block__text{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            h2{
              @include fontChar($black, 25px, 700);
              margin-bottom: 25px;
            }
            p{
              line-height: 1.5;
              @include fontChar(#3D3D3D, 18px, 400);
              min-height: 9rem;
            }
          }
        }
      }
    }
    .btn__wrapper{
      width: 100%;
      display: flex;
      justify-content: center;
      margin-top: 4.5rem;
      @media (max-width: 992px) {
        margin-top: 2rem;
      }
    }
  }
}

.services_blog_wrapper{
  margin-top: 100px;
  position: relative;
  overflow: hidden;
  .background__figure{
    position: absolute;
    width: fit-content;
    height: fit-content;
    z-index: -2;
    top: 18%;
    left: -26%;
    img{
      max-width: 100%;
      object-fit: cover;
      display: block;
    }
    @media (max-width: 992px) {
      top: 32%;
    }
    @media (max-width: 787px) {
      @include adaptive-value("top", 70, 109, 1);
    }
  }
  .services{
    position: relative;
    z-index: 5;
    .container{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      .services__paragraph{
        margin-bottom: 55px;
      }
      .cards__inner{
        display: flex;
        justify-content: space-between;
        width: 100%;
        align-items: stretch;
        @media (max-width: 992px) {
          flex-direction: column;
          flex-wrap: wrap;
        }
        .article{
          width: 100%;
          max-width: 330px;
          display: flex;
          flex-direction: column;align-items: center;
          box-shadow: 0px 38px 38px -11px rgba(0, 0, 0, 0.31);
          border-radius: 36px;
          padding: 50px 30px;
          cursor: pointer;
          transition: all .4s ease;
          position: relative;
          overflow: hidden;
          @media (max-width: 992px) {
            max-width: 48%;
          }
          @media (max-width: 787px) {
            max-width: 350px;
          }
          &:nth-child(2) {
            margin: 0 20px;
            @media (max-width: 992px) {
              margin: 0 !important;
            }
            @media (max-width: 787px) {
              margin-top: 2rem !important;
            }
          }
          &::before{
            content: '';
            background: $blue;
            transition: .8s ease;
            width: 100%;
            height: 0;
            border-radius: 36px;
            position: absolute;
            z-index: -1;
            bottom: 0;
            left: 0;
          }
          &::after{
            content: '';
            width: 100%;
            height: 100%;
            background: $wh;
            top: 0;
            left: 0;
            position: absolute;
            z-index: -4;
          }
          &:hover {
            &::before {
              height: 100%;
              bottom: auto;
              top: 0;
            }
            .icon__wrapper{
              background: $yellow;
            }
            *{
              color: $wh !important;
            }
            li::before{
              background: $wh;
            }
          }
          *{
            color: $black;
            transition: all .4s ease;
          }
          .icon__wrapper{
            width: 135px;
            height: 135px;
            background: $blue;
            display: flex;
            align-items: center;
            justify-content: center;
            box-shadow: 0px 6px 18px 5px rgba(0, 0, 0, 0.25);
            margin-bottom: 60px;
            border-radius: 50%;
          }
          h2{
            @include fontChar($black, 25px, 700);
          }
          ul{
            margin-top: 60px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            li{
              font-size: 18px;
              font-weight: 400;
              position: relative;
              padding-left: 15px;
              &:not(:last-child) {
                margin-bottom: 5px;
              }
              &::before{
                content: '';
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                transition: all .4s ease;
                left: 0;
                width: 5px;
                height: 5px;
                flex: 0 0 5px;
                border-radius: 50%;
                background: $black;
              }
            }
          }
          button{
            width: 60px;
            height: 60px;
            border-radius: 50%;
            border: none;
            outline: none;
            display: flex;
            align-items: center;
            justify-content: center;
            background: $wh;
            transition: all .4s ease;
            cursor: pointer;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            margin-top: 60px;
            &:hover {
              background: $yellow;
            }
          }
        }
        .additional__col{
          width: 100%;
          max-width: 330px;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          @media (max-width: 992px) {
            flex-grow: 1;
            width: 100%;
            max-width: 100%;
            flex-direction: row;
            justify-content: space-between;
          }
          @media (max-width: 787px) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
          }
          .article{
            padding: 50px 0;
            width: 100%;
            box-shadow: 0px 38px 38px -11px rgba(0, 0, 0, 0.25) !important;
            &:not(:last-child) {
              margin-bottom: 20px;
              @media (max-width: 992px) {
                margin-bottom: 0;
              }
            }
          }
        }
        .tablet__version{
          width: 100%;
          align-items: stretch;
          justify-content: space-between;
          margin-bottom: 2rem;
          @media (min-width: 992px) {
            display: none;
          }
          @media (max-width: 992px) {
            display: flex;
          }
          @media (max-width: 787px) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
          }
        }
      }
    }
  }
  .blog{
    margin-top: 100px;
    position: relative;
    z-index: 5;
    .container{
      display: block;
      .blog__inner{
        margin-top: 55px;
        width: 100%;
        .blog__swiper{
          display: flex;
          align-items: stretch;
          justify-content: center;
          padding-bottom: 4rem;
          :global(.swiper-pagination-bullet-active) {
            background: $blue !important;
            transform: scale(1.6);
          }
          :global(.swiper-pagination-bullet) {
            background: $blue;
            opacity: 1;
            transition: all .3s ease;
          }
          .blog__swiper__slide{
            display: flex;
            justify-content: center;
          }
          .article{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            cursor: pointer;
            @media (max-width: 992px) {
              width: 100%;
              max-width: 17.7rem;
            }
            &:hover {
              figure {
                img{
                  transform: scale(1.2);
                }
              }
            }
            figure{
              display: flex;
              width: fit-content;
              height: fit-content;
              overflow: hidden;
              border-radius: 30px;
              img{
                max-width: 100%;
                display: block;
                object-fit: cover;
                transition: transform .4s ease;
              }
            }
            .addition_data{
              display: flex;
              align-items: center;
              justify-content: space-between;
              width: 100%;
              margin-top: 1rem;
              *{
                color: #929292;
                font-weight: 500;
                font-size: 16px;
              }
              .views{
                display: flex;
                align-items: center;
                justify-content: center;
                p{
                  margin-left: 5px;
                }
              }
            }
            .about__article{
              display: flex;
              flex-direction: column;
              align-items: flex-start;
              margin-top: 1rem;
              h2{
                @include fontChar($black, 25px, 700);
                margin-bottom: 5px;
                line-height: 1.2;
              }
              p{
                @include fontChar(#323232, 18px, 400);
                line-height: 1.5;
                margin-top: 10px;
              }
            }
          }
        }
      }
      .btn__wrapper{
        width: 100%;
        margin-top: 4.5rem;
        display: flex;
        justify-content: center;
        @media (max-width: 992px) {
          margin-top: 2rem;
        }
      }
    }
  }
}

.clients_and_reviews{
  margin-top: 100px;
  .container{
    display: block;
    .cards__inner{
      width: 100%;
      margin-top: 4.5rem;
      .top__block{
        width: 100%;
        .initial__block{
          display: none;
          width: 100%;
          flex-wrap: wrap;
          width: 100%;
          .col__wrapper{
            width: 50%;
            display: flex;
            justify-content: center;
            img{
              display: block;
              max-width: 100%;
              object-fit: cover;
            }
            &:nth-child(odd) {
              border-bottom: 2px solid #D1D1D1 !important;
              border-right: 2px solid #D1D1D1 !important;
              border-left: none !important;
              border-top: none !important;
            }
            &:nth-child(even) {
              border-bottom: 2px solid #D1D1D1 !important;
              border-right: none !important;
              border-left: none !important;
              border-top: none !important;
            }
          }
          @media (max-width: 992px) {
            display: flex;
          }
        }
        .sub__block{
          width: 100%;
          display: grid;
          grid-template-columns: repeat(4, 1fr);
          @media (max-width: 992px) {
            display: none !important;
          }
          &:nth-child(1){
            border-bottom: 2px solid #D1D1D1;
          }
          .col__wrapper{
            display: flex;
            justify-content: center;
            align-items: center;
            min-height: 10rem;
            &:not(:last-child) {
              border-right:  2px solid #D1D1D1;
            }
            @media (max-width: 787px) {
              width: 50%;
            }
          }
        }
      }
      .bottom__block{
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        border-top: 2px solid #D1D1D1;
        @media (max-width: 992px) {
          border-top: none;
        }
        @media (max-width: 992px) {
          display: flex;
          align-items: center;
          justify-content: center;
        }
        .review__block{
          padding: 56px 62px 0 0;
          border-right: 2px solid #D1D1D1;
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          @media (max-width: 992px) {
            border-right: none;
            padding: 70px 0 0 0;
          }
          .about__reviewer{
            display: flex;
            align-items: center;
            justify-content: flex-start;
            figure{
              border-radius: 50%;
              width: fit-content;
              height: fit-content;
              filter: drop-shadow(0px 20px 30px rgba(76, 64, 247, 0.2));
              img{
                max-width: 100%;
                display: block;
                border-radius: 50%;
                object-fit: cover;
              }
            }
            .initials{
              flex-direction: column;
              display: flex;
              align-items: flex-start;
              margin-left: 2rem;
              h2{
                @include fontChar($black, 25px, 700);
                margin-bottom: px;
              }
              p{
                @include fontChar(#888888, 16px, 500);
              }
            }
          }
          .comments{
            @include fontChar($black, 18px, 400);
            line-height: 1.6;
            margin-top: 1rem;
          }
          .rating{
            margin-top: 1rem;
            img{
              &:not(:last-child) {
                margin-right: 10px;
              }
            }
          }
        }
        .companies{
          @media (max-width: 992px) {
            display: none;
          }
          .sub__block{
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            &:nth-child(1) {
              border-bottom: 2px solid #D1D1D1;
            }
            .col__wrapper{
              display: flex;
              justify-content: center;
              min-height: 10rem;
              img{
                max-width: 100%;
                display: block;
                object-fit: cover;
              }
              &:nth-child(odd) {
                border-right: 2px solid #D1D1D1;
              }
            }
          }
        }
        .review__swiper{
          display: none;
          width: 100%;
          padding-bottom: 4rem;
          @media (max-width: 992px) {
            display: block;
          }
          :global(.swiper-pagination-bullet-active) {
            background: $blue !important;
            transform: scale(1.6);
          }
          :global(.swiper-pagination-bullet) {
            background: $blue;
            opacity: 1;
            transition: all .3s ease;
          }
        }
      }
    }
  }
}

.scroll__article{
  margin-top: 100px;
  width: 100%;
  .container{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    & > div {
      height: 210px;
      overflow-y: scroll;
      &::-webkit-scrollbar{
        width: 15px;
        cursor: pointer;
        &-track {
          background: #C0C0C0;
          border-radius: 18px;
        }
        &-thumb {
          background: $blue;
          border-radius: 18px;
          cursor: pointer;
        }
      }
    }
    h3{
      @include fontChar($black, 25px, 700);
      margin-bottom: 5px;
    }
    p{
      @include fontChar($black, 18px, 400);
      line-height: 1.6;
    }
  }
}

.discuss{
  width: 100%;
  margin-top: 100px;
  position: relative;
  .background__figure__wrapper{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    overflow: hidden;
    .background__figure{
      position: absolute;
      left: -6%;
      bottom: -27rem;
      @media (min-width: 992px) {
        @include adaptive-value("bottom", -30, -2,1);
      }
      @media (max-width: 992px) {
        bottom: -13rem;
      }
      img{
        max-width: 100%;
        object-fit: cover;
        display: block;
        @media (max-width: 992px) {
          max-width: 49rem;
        }
      }
    }
  }
  .container{
    display: block;
    position: relative;
    padding: 0 23px 90px;
    .man__wrapper{
      width: fit-content;
      height: fit-content;
      position: absolute;
      bottom: -2rem;
      right: 0;
      z-index: 5;
      @media (max-width: 992px) {
        display: none;
      }
    }
  }
}

.privacy__policy{
  margin: 0 0 50px;
  .container{
    .router{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      width: 100%;
      margin-bottom: 30px;
      .router__link{
        @include fontChar($black, 16px, 400);
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-right: .5rem;
        &:not(:first-child) {
          &::before{
            content: '';
            background: url("../assets/img/svg/arrow_right.svg") no-repeat center;
            width: 15px;
            height: 15px;
            transform: scale(1.3);
            margin: 0 .5rem 2px .2rem;
          }
        }
        &:last-child{
          color: #9C9C9C;
          cursor: text;
        }
        &:nth-child(1),
        &:nth-child(2) {
          &:hover {
            color: $blue;
            cursor: pointer;

          }
        }
      }
    }
    .main__block{
      display: block;
      width: 100%;
      .title{
        margin-bottom: 30px;
      }
      p{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        span{
          @include fontChar($black, 16px, 400);
          &:not(:last-child) {
            margin-bottom: 20px;
          }
        }
      }
    }
    .block__includes{
      margin-top: 30px;
      list-style: none;
      li span.title{
        @include fontChar($black, 24px, 600);
        margin-bottom: 2rem;
      }
      .second__col{
        margin-top: 30px;
      }
      li{
        &:nth-child(2) {
          ul{
            li{
              display: flex;
              flex-direction: column;
              &.row{
                flex-direction: row;
                display: flex;
                &.no_margin{
                  margin-top: 0;
                }
              }
            }
          }
        }
        ul{
          margin: 0 0 0 50px;
          @media (max-width: 470px) {
            margin: 0 0 0 20px;
          }
          li{
            @include fontChar($black, 16px, 400);
            margin-top: 20px;
            line-height: 1.6;
            display: flex;
            span{
              margin-right: 1rem;
            }
          }
        }
      }
    }
  }
}

.header__contact{
  padding: 50px 0 !important;
  @media (max-width: 992px) {
    padding: 40px 0 !important;
  }
  nav{
    padding: 0 !important;
  }
}

.contact__us__page{
  margin: 0 0 50px;
  .container{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .router{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      width: 100%;
      margin-bottom: 30px;
      .router__link{
        @include fontChar($black, 16px, 400);
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-right: .5rem;
        transition: all .3s ease;
        &:not(:first-child) {
          &::before{
            content: '';
            background: url("../assets/img/svg/arrow_right.svg") no-repeat center;
            width: 15px;
            height: 15px;
            transform: scale(1.3);
            margin: 0 .5rem 2px .2rem;
          }
        }
        &:last-child{
          color: #9C9C9C;
        }
        &:nth-child(1){
          &:hover {
            color: $blue;
            cursor:pointer;
          }
        }
      }
    }
    .experience__inner{
      display: flex;
      width: 100%;
      align-items: flex-start;
      justify-content: space-between;
      @media (max-width: 992px) {
        flex-direction: column;
        align-self: center;
        justify-content: center;
      }
      .left{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        @media (max-width: 992px) {
          align-self: center;
          width: 100%;
          max-width: 490px;
          margin-bottom: 1rem;
        }
        h2{
          @include fontChar($black, 32px, 600);
        }
        .switch__block{
          align-self: flex-start;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-top: 30px;
          button{
            border: none;
            @include fontChar(#9C9C9C, 16px, 400);
            position: relative;
            cursor: pointer;
            transition: color .3s ease;
            &:hover {
              color: $blue;
            }
            @media (max-width: 400px) {
              padding-right: 12px;
              margin-right: 7px;
              font-size: 14px;
            }
            &.active_btn{
              color: $blue;
            }
            &:not(:last-child) {
              padding-right: 17px;
              margin-right: 10px;
              &::before {
                content: '';
                width: 7px;
                position: absolute;
                height: 7px;
                flex: 0 0 7px;
                border-radius: 50%;
                background: $blue;
                top: 50%;
                transform: translateY(-50%);
                right: 0;
              }
            }
          }
        }
        .content__tabs{
          display: flex;
          align-items: flex-start;
          justify-content: center;
          margin-top: 26px;
          @media (max-width: 430px) {
            flex-direction: column;
          }
          .col{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            &:nth-child(1) {
              margin-right: 50px;
            }
            h3{
              @include fontChar($black, 18px, 600);
              margin-bottom: 20px;
            }
            div{
              display: flex;
              flex-direction: column;
              align-items: flex-start;
              .type{
                @include fontChar($black, 16px, 500);
                margin-bottom: 10px;
              }
              .link__to__social{
                display: flex;
                flex-direction: row !important;
                margin-bottom: 20px;
                align-items: center;
                cursor: pointer;
                *{
                  color: #9C9C9C;
                  border-color: #9C9C9C;
                  transition: all .3s ease;
                }
                &:hover * {
                  color: $blue;
                  border-color: $blue !important;
                }
                div, span{
                  @include fontChar(#9C9C9C, 16px, 400);
                  line-height: 1.2;
                }
                svg{
                  width: 12px;
                  height: 12px;
                  color: #9C9C9C;
                }
                .icon__wrapper{
                  width: 20px;
                  height: 20px;
                  border: 1.5px solid #9C9C9C;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  border-radius: 50%;
                  margin-right: 5px;
                }
              }
            }
          }
        }
      }
      .right{
        width: 100%;
        max-width: 540px;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        @media (max-width: 1100px) {
          max-width: 490px;
        }
        @media (max-width: 992px) {
          align-self: center;
        }
        h2{
          @include fontChar($black, 32px, 600);
          align-self: flex-start;
        }
        .form{
          margin-top: 30px;
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          .field{
            &:nth-child(1) {
              margin-bottom: 20px !important;
            }
          }
          .field, .textarea__field{
            position: relative;
            width: 100%;
            display: flex;
            flex-direction: column-reverse;
            align-items: flex-start;
            &:not(:last-child) {
              margin-bottom: 44px;
            }
            input, textarea{
              padding: 20px;
              resize: none;
              width: 100%;
              border: 1px solid #CCCCCC;
              border-radius: 10px;
              background: $wh;
              color: $black !important;
              font-size: 16px !important;
              font-weight: 400 !important;
              &:focus{
                border-color: $blue;
              }
            }
            label{
              display: flex;
              flex-direction: row-reverse;
              color: #9C9C9C !important;
              font-size: 16px !important;
              font-weight: 400 !important;
              z-index: 3;
              margin-bottom: 5px;
              cursor: text;
              &::before {
                content: '*';
                color: red;
                margin-left: 5px;
              }
            }
          }
          .col{
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media (max-width: 992px) {
              flex-direction: column;
              align-items: center;
            }
            .field{
              margin-bottom: 0 !important;
              width: 48%;
              @media (max-width: 992px) {
                width: 100%;
              }
              &:nth-child(1) {
                @media (max-width: 992px) {
                  margin-bottom: 20px !important;
                }
                label::before{
                  content: '';
                }
              }
            }
          }
          .textarea__field{
            width: 100%;
            margin-top:22px;
            position: relative;
            margin-bottom: 25px !important;
            &::before {
              content: "";
              position: absolute;
              top: 3rem;
              left: 0;
              padding-left: 1.52rem;
              width: 88%;
              height: 65%;
              z-index: 9;
              background: repeating-linear-gradient(to bottom, #999 0 1px, transparent 1px 33px) 0 26px/100% content-box no-repeat;
              @media (max-width: 460px) {
                width: 80%;
              }
            }
            textarea {
              height: 244px;
              font-size: 16px !important;
              font-weight: 400 !important;
              color: $black !important;
              font-family: $inter !important;
              background: transparent;
              position: relative;
              z-index: 11;
              padding: 30px 30px 50px 30px;
              line-height: 2.11em;
              &:focus{
                border-color: $blue;
              }
            }
          }
          .btn__wrapper {
            display: flex;
            justify-content: center;
            .submit__btn {
              padding: 10px 35px;
              background: $blue;
              border-radius: 10px;
              cursor: pointer;
              position: relative;
              overflow: hidden;
              border: none;
              span {
                @include fontChar($wh, 16px, 600);
                position: relative;
                z-index: 3;
              }

              &:hover {
                &::before {
                  width: 100%;
                  left: 0;
                  right: auto;
                }
              }

              &::before {
                content: '';
                width: 0;
                transition: .5s ease;
                height: 100%;
                background: $yellow;
                position: absolute;
                top: 0;
                right: 0;
                z-index: 1;
              }
            }
          }
        }
      }
    }
  }
}