@import "../../scss/fonts.scss";
@import "../../scss/mixins.scss";

.form{
  margin-top: 4.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  @media (max-width: 992px) {
    margin-top: 2rem;
  }
  .text__forms{
    width: 100%;
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    @media (max-width: 787px) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
  .data__fields{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 48%;
    @media (max-width: 787px) {
      width: 100%;
      max-width: 500px;
      margin-bottom: 1rem;
    }
  }
  .text__field{
    width: 48%;
    overflow: hidden;
    position: relative;
    backdrop-filter: blur(13.5px);
    border-radius: 16px;
    display: flex;
    background: linear-gradient(138.13deg, rgba(255, 255, 255, 0.37) 2.6%, rgba(217, 217, 217, 0) 119.97%);

    &::before{
      content: "";
      position: absolute;
      top: 2rem;
      left: 0;
      padding: 0 3rem;
      width: 74%;
      height: 77%;
      z-index: 9;
      background: repeating-linear-gradient(to bottom, #999 0 1px, transparent 1px 39px) 0 26px/100% content-box no-repeat;
      @media (max-width: 992px) {
        top: 1.6rem;
        padding: 0 1.5rem;
        width: 85%;
      }
      @media (max-width: 787px) {
        width: 87%;
      }
    }
    @media (max-width: 787px) {
      width: 100%;
      max-width: 500px;
    }
    *{
      color: $black;
    }
    label{
      font-size: 25px;
      position: absolute;
      top: 1.7rem;
      left: 3rem;
      z-index: 3;
      font-weight: 400;
      cursor: text;
      @media (max-width: 992px) {
        top: 1.2rem;
        left: 1.4rem;
      }
    }
    textarea{
      resize: none;
      height: 100%;
      position: relative;
      z-index: 11;
      outline: none;
      padding: 1.5rem 3rem;
      width: 100%;
      line-height: 1.55em;
      border: 1px solid #8A8A8A;
      border-radius: 16px;
      color: $black !important;
      font-size: 25px !important;
      font-weight: 400 !important;
      background: transparent;
      font-family: $inter !important;
      &:focus {
        border-color: $blue;
      }
      @media (max-width: 992px) {
        padding: 1rem 1.5rem;
      }
      @media (max-width: 787px) {
        height: 157px;
      }
    }
  }
  .field{
    position: relative;
    width: 100%;
    &:not(:last-child) {
      margin-bottom: 1rem;
    }
    &:nth-child(2) {
      label::before{
        content: '';
      }
    }
    label{
      position: absolute;
      top: 1.5rem;
      left: 3rem;
      display: flex;
      flex-direction: row-reverse;
      color: $black !important;
      font-size: 25px !important;
      font-weight: 400 !important;
      z-index: 3;
      cursor: text;
      @media (max-width: 992px) {
        top: 1rem;
        left: 1.5rem;
      }
      &::before {
        content: '*';
        color: red;
        margin-left: 5px;
      }
    }
    input {
      padding: 1.5rem 3rem;
      width: 100%;
      border: 1px solid #8A8A8A;
      border-radius: 16px;
      background: linear-gradient(138.13deg, rgba(255, 255, 255, 0.37) 2.6%, rgba(217, 217, 217, 0) 119.97%);
      backdrop-filter: blur(13.5px);
      color: $black !important;
      font-size: 25px !important;
      font-weight: 400 !important;
      &:focus {
        border-color: $blue;
      }
      @media (max-width: 992px) {
        padding: 1rem 1.5rem;
      }
    }
  }
  .btn__wrapper{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 4.5rem;
    @media (max-width: 992px) {
      margin-top: 2rem;
    }
    .btn__submit{
      padding: 15px 58px;
    }
  }
}