@import '../../scss/mixins.scss';
@import '../../scss/fonts.scss';

.footer{
  background: linear-gradient(262.31deg, #EAAA31 -10.67%, #03BCFF 119.67%);
  margin-bottom: -2rem;
  .container{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 50px 23px;
    @media (max-width: 992px) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    .footer__inner{
      width: 100%;
      align-self: flex-start;
      align-items: flex-start;
      justify-content: space-between;
      display: none;
      @media (max-width: 992px) {
        display: flex;
      }
      .left{
        width: 50%;
        @media (max-width: 600px) {
          width: 100%;
          max-width: 9rem;
        }
        .col{
          width: 100%;
          justify-content: center;
        }
      }
      .right{
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        @media (max-width: 600px) {
          width: 100%;
          max-width: 7rem;
        }
        .col{
          width: 100%;
          justify-content: center;
          align-items: flex-start;
          ul{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            li{
              span{
                color: $wh !important;
                font-size: 16px !important;
                font-weight: 400 !important;
              }
            }
          }
        }
      }
    }
    .col{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
      ul{
        li{
          span{
            color: $wh !important;
            font-size: 16px;
            font-weight: 400;
          }
        }
      }
      @media (min-width: 993px) {
        &:nth-child(1) {
          margin-right: 3rem;
        }
        &:nth-child(5) {
          margin-left: 3rem;
        }
      }
      h3{
        @include fontChar($wh, 18px, 700);
      }
      .socials{
        display: flex;
        margin: 13px 0 0 0 !important;
        li{
          &:not(:last-child) {
            margin-right: 10px;
          }
          a{
            width: 27px;
            height: 27px;
            border: 2px solid $wh;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex: 0 0 27px;
            transition: all .3s ease;
            svg{
              width: 13px;
              height: 13px;
            }
            &:hover {
              border-color: $yellow;
              background: $yellow;
            }
          }
        }
      }
      ul{
        margin: 20px 0 25px;
        li{
          &:not(:last-child) {
            margin-bottom: 6px;
          }
          a{
            @include fontChar($wh, 16px, 400);
            font-family: $inter;
            border-bottom: 1px solid transparent;
            padding-bottom: 2px;
            &:hover {
              border-bottom-color: $wh;
            }
            @media (max-width: 420px) {
              font-size: 15px;
            }
            .lower{
              font-size: 13px;
            }
          }
        }
      }
    }
    .logo{
      width: 100%;
      display: flex;
      justify-content: center;
      @media (max-width: 993px) {
        margin-bottom: 2rem;
      }

      a{
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background: $wh;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}